import jss from 'jss';
import jssGlobal from 'jss-plugin-global';
import jssNested from 'jss-plugin-nested';
import { ParameterCheckbox } from './checkbox';

/**
 * 转换默认值为布尔型
 * @param value 默认值
 */
export function transformValue2Boolean(value: any): boolean {
    let result: boolean;

    if (value === 'true' || value === true) {
        result = true;
    } else {
        result = false;
    }

    return result;
}

/**
 * 设置样式
 * @param style 样式
 */
export function setCheckboxStyle({ opacity, background, color }: {
    opacity?: number;
    background?: string;
    color?: string;
}) {
    jss.use(jssGlobal(), jssNested());

    const cls = `${ParameterCheckbox.BASE_CLS}-${BI.UUID()}`;

    const styles4Attach: {
        color?: string;
        background?: string;
        opacity?: string;
    } = {};

    if (opacity) {
        styles4Attach.opacity = `${opacity}`;
    }
    
    if (background) {
        styles4Attach.background = `${background}`;
    }

    if (color) {
        styles4Attach.color = `${color}`;
    }

    jss.createStyleSheet({
        '@global': {
            [`.${cls}`]: styles4Attach,
        },
    }).attach();

    return cls;
}
